.header {
    background: #a8000059;
    .container {
        padding: 0 15px;
    }

    &__inner {
        display: flex;
        align-items: center;
        padding: 10px 0;
    }
    &__btns{
        display: flex;
        gap: 15px;
        margin-left: auto;
    }
    &__btn{
        border: none;
        background: #FF0000;
        color: #fff;
        padding: 6px 10px;
        cursor: pointer;
    }
}